import { mapGetters } from "vuex";

export default {
    data() {
        return {
            picUrl: '',
            text: ''
        }
    },
    computed: {
        ...mapGetters('mallConfig', {
            __wxapp_img: 'getWxappImg'
        })
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.picUrl = this.__wxapp_img.mall.disabled;
        this.text = options.text;
    }
}
